.cart{
    position: absolute;
    right: 20px;
    top: 80px;
    z-index: 999;
    background-color: white;
    padding: 20px;
    -webkit-box-shadow: 2px 0px 11px -2px rgba(0, 0, 0, 0.5);
        box-shadow: 2px 0px 11px -2px rgba(0, 0, 0, 0.5);

    h1{
        margin-bottom: 30px;
        color:gray;
        font-weight: 400;
        font-size: 24px;

    }

    .item{
        display: flex;
        align-items: center;
        gap: 20px;
        margin-bottom: 30px;

        img{
            width: 80px;
            height: 100px;
            object-fit: cover;
        }
        .details{
            h1{
                font-size: 18px;
                font-weight: 500;
            }
            p{
                color: gray;
                margin-bottom: 10px;
                font-size: 14px;
            }
            .price{
                color: #2879fe;
            }
        }

        .delete{
            color:red;
            font-size: 30px;
            cursor: pointer;
        }
    }
    .total{
        display: flex;
        justify-content:space-between;
        font-weight: 500;
        margin-bottom: 20px;
        font-size: 18px;
    }

    button{
        width: 250px;
            background-color: #2879fe;
            padding: 10px;
            color: white;
            display: flex;
            align-items: center;
            gap: 20px;
            justify-content: center;
            cursor: pointer;
            border: none;
            font-weight: 500;
            margin-bottom: 20px;
    }

    .reset{
        color: red;
        font-size: 12px;
    }
}